
@charset "utf-8";
@import "//unpkg.com/mint-ui/lib/style.css";
html {
  font-family: Helvetica,STHeiti STXihei, Microsoft JhengHei, Microsoft YaHei, Arial;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0 auto;
  padding: 0px;
  font-size: 12px;
  font-family: "Microsoft YaHei" ! important;
  /*text-align:center; */
  min-width: 320px;
  max-width: 640px;
}
p,
ul,
ol,
li,
dl,
dt,
dd,
h1,
h2,
h3,
h4,
h5,
form,
fieldset,
legend,
input,
select,
textarea,
button,
th,
td,
menu,
article,
pre,
xmp,
plaintext,
listing {
  margin: 0;
  padding: 0;
}
pre,
xmp,
plaintext,
listing {
  white-space: normal;
}
article,
aside,
dialog,
figure,
footer,
header,
hgroup,
nav,
section,
select,
time {
  display: block;
}
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
button,
label {
  font-size: 100%;
  font-weight: 100;
  vertical-align: middle;
}
ul,
dl,
ol {
  list-style: none;
}
i,
em {
  font-style: normal;
}
b {
  font-weight: normal;
}
img,
fieldset,
input[type="submit"] {
  border: none;
}
input {
  outline: none;
  background: transparent;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  cursor: pointer;
  border: none;
}
textarea {
  word-wrap: break-word;
  resize: none;
}
/* 去除IE下拉菜单箭头 */
select::-ms-expand {
  display: none;
}
/* 去除IE下radio、checkbox默认样式 */
::-ms-check {
  display: none;
}
/* 去除输入框、文本域默认边框和IOS4下的内阴影 */
input,
textarea,
select {
  -webkit-appearance: none;
  border: none;
  outline: none;
}
/* 设置Gecko内核下输入框属性placeholder中值的文件颜色 */
input:-moz-placeholder {
  color: #C4C4C4;
}
/* 设置webkit内核下输入框属性placeholder中值的文件颜色 */
::-webkit-input-placeholder {
  color: #C4C4C4;
}
/* 设置webkit内核下文字大小不小于12px */
html,
body {
  -webkit-text-size-adjust: none;
  /*height:100%;*/
}
/* 禁止长按链接、按钮、输入框出现系统默认弹框菜单 */
a,
button,
input {
  -webkit-touch-callout: none;
  outline: none;
}
a {
  text-decoration: none;
  -webkit-touch-callout: none;
  color: #666;
}
* {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
  font-family: "Microsoft YaHei" ! important;
}
.ui-table {
  display: table;
}
.ui-cell {
  display: table-cell;
  vertical-align: middle;
}
html.debug_class #__vconsole {
  display: block;
}
html #__vconsole {
  display: none;
}
.message {
  display: none;
  position: fixed;
  top: 40%;
  left: 0;
  right: 0;
  width: 70%;
  background-color: rgba(0, 0, 0, 0.51);
  margin: 0 auto;
  color: #fff;
  text-align: center;
  border-radius: 0.15625rem;
  padding: 0.5rem;
  z-index: 999;
}
.message.active {
  display: block;
}
.loading {
  display: none;
  position: fixed;
  top: 40%;
  left: 0;
  right: 0;
  width: 50%;
  background-color: rgba(0, 0, 0, 0.51);
  margin: 0 auto;
  color: #fff;
  text-align: center;
  border-radius: 0.15625rem;
  padding: 0.5rem;
  z-index: 999;
}
.loading.active {
  display: block;
}
html,
body {
  width: 100%;
  height: 100%;
  color: #333;
  font-size: 0.4rem;
  background: #f8f8f8;
  overflow: hidden;
}
.container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.base_header {
  height: 1.06666667rem;
  line-height: 1.06666667rem;
  text-align: center;
  position: relative;
  border-bottom: 1px solid #ccc;
  background: #fff;
}
.base_header .btn_login {
  position: absolute;
  top: 0;
  right: 0.4rem;
  font-size: 0.34666667rem;
  color: #333;
  display: block;
}
.btn_back {
  position: absolute;
  top: 0;
  left: 0.4rem;
  width: 0.4rem;
  height: 100%;
  background: url("../images/icon_back.png") center no-repeat;
  background-size: contain;
}
.btn_dismiss {
  position: absolute;
  top: 0;
  left: 0.4rem;
  width: 0.29333333rem;
  height: 100%;
  background: url("../images/icon_back.png") center no-repeat;
  background-size: contain;
}
/** 搜索框&购物车头部 **/
.header {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  -webkit-box-pack: justify;
  box-pack: justify;
  justify-content: space-between;
  height: 1.06666667rem;
  -ms-flex-align: center;
  -webkit-box-align: center;
  box-align: center;
  align-items: center;
  padding: 0 0.4rem;
  border-bottom: none;
  background-color: #fff;
}
.header .search_contaienr {
  margin-left: 0.53333333rem;
  position: relative;
}
.header input[type="text"],
.header input[type="search"] {
  border: 0;
}
.header .search_input {
  position: relative;
  padding-left: 0.8rem;
  height: 0.88rem;
  width: 6.53333333rem;
  border-radius: 2.66666667rem;
  background: url("../images/index/icon_search.png") #F3F3F3 0.26666667rem center no-repeat;
  background-size: 0.4rem;
}
.header .search_input + .btn_delete {
  position: absolute;
  right: 0.26666667rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.29333333rem;
  background: url(../images/btn_icon_delete.png) center / contain no-repeat;
}
.header .shop_car {
  display: block;
  margin: 0 0.4rem;
  width: 0.66666667rem;
  height: 0.66666667rem;
  background: url("../images/icon_shopcar.png") center center no-repeat;
  background-size: contain;
}
.header .navbar_toggle {
  display: block;
  border: none;
  outline: none;
  background: none;
  width: 0.8rem;
  text-align: center;
}
.header .title {
  margin: 0 auto;
  font-size: 0.48rem;
  color: #333;
  font-size: 0.4rem;
  line-height: 1.06666667rem;
}
.header .nav_dot {
  display: inline-block;
  width: 0.10666667rem;
  height: 0.10666667rem;
  border-radius: 50%;
  background-color: #505050;
}
.header .message_box button {
  border: none;
  outline: none;
  background: transparent;
  color: #fff;
  margin-top: 0.21333333rem;
  letter-spacing: 1px;
}
.header .shop_car {
  position: relative;
}
.header .shop_car .shop_count {
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  right: -0.4rem;
  top: 0;
  background: #FE403B;
  padding: 0 0.05333333rem;
  color: #fff;
  text-align: center;
  font-size: 0.26666667rem;
  line-height: 1.4;
  border-radius: 15px;
  letter-spacing: 1px;
  display: none;
}
.header .btn_delete {
  position: absolute;
  width: 0.24rem;
  height: 0.24rem;
  background: url("../images/icon_delete.png") center / contain no-repeat;
  right: 0.26666667rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.header .navbar_menue {
  position: absolute;
  top: 0.53333333rem;
  right: 10px;
  width: 2.48rem;
  background: #fafafa;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  z-index: 1000;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  display: none;
}
.header .navbar_menue_active {
  display: block;
  -webkit-animation: navbar_animate_show .5s ease;
  -webkit-animation-fill-mode: forwards;
  animation: navbar_animate_show .5s ease;
  animation-fill-mode: forwards;
}
@-webkit-keyframes navbar_animate_show {
0% {
    -webkit-opacity: 0;
}
100% {
    -webkit-opacity: 1;
    top: 1.28rem;
}
}
@keyframes navbar_animate_show {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
    top: 1.28rem;
}
}
.header .navbar_menue li a {
  display: block;
  height: 1.09333333rem;
  border-bottom: 0.05333333rem solid #f7f7f7;
  font-size: 12px;
  color: #030303;
  line-height: 12px;
  text-align: center;
  line-height: 4;
  letter-spacing: 1px;
}
.header .menue_home,
.header .menue_search {
  display: inline-block;
  width: 0.53333333rem;
  height: 0.56rem;
  vertical-align: middle;
  margin-right: 5px;
}
.header .menue_home {
  background: url("../images/icon_menue_home.png") center top no-repeat;
  background-size: 0.45333333rem;
  height: 0.50666667rem;
}
.header .menue_search {
  background: url("../images/icon_menue_search.png") center no-repeat;
  background-size: 0.45333333rem;
}
.header .navbar_menue li:last-child a {
  border-bottom: none;
}
.header .icon_arrow {
  position: absolute;
  top: -0.21333333rem;
  right: 0.21333333rem;
  width: 0;
  height: 0;
  border-bottom: 0.26666667rem solid #fafafa;
  border-left: 0.26666667rem solid transparent;
  border-right: 0.26666667rem solid transparent;
}
/** end **/
.hide {
  display: none!important;
}
.show {
  display: block!important;
}
/** 轮播 **/
.swiper-container {
  position: relative;
  height: 100%;
}
.swiper-container img {
  display: block;
  width: 100%;
  height: 100%;
}
.swiper-pagination {
  text-align: right;
}
.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin-left: 0;
  margin-right: 0.13333333rem;
  background-color: #fff;
  opacity: 0.3;
}
.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet :nth-of-type(:last-child) {
  margin-right: 0.26666667rem;
}
.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 0.13333333rem;
}
.swiper-pagination-bullet-active {
  background-color: #fff;
  opacity: 1!important;
}
/** end **/
.product_buy {
  position: fixed;
  display: none;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  padding: 0.8rem 1.06666667rem 0.53333333rem;
  width: 100%;
  padding-top: 0.48rem;
  height: 6.72rem;
  background-color: #fff;
  text-align: center;
  z-index: 1000;
}
.product_buy input {
  border: 1px solid #eee;
  width: 4.53333333rem;
  border-radius: 25px 25px;
  height: 0.93333333rem;
}
.product_buy .left {
  float: left;
}
.product_buy .right {
  float: right;
}
.product_buy p:nth-child(1),
.product_buy p:nth-child(2) {
  border-bottom: 1px solid #efefef;
  padding-bottom: 0.48rem;
}
.btn_reduce_number {
  display: inline-block;
  margin-top: 0.17333333rem;
  width: 0.85333333rem;
  height: 0.8rem;
  background: url("../images/icon_reduce.jpg") center / contain no-repeat;
}
.btn_reduce_number_ok {
  background: url("../images/icon_reduce_ok.jpg") center / contain no-repeat;
}
.btn_add_number {
  display: inline-block;
  width: 0.85333333rem;
  height: 0.8rem;
  margin-top: 0.17333333rem;
  background: url("../images/icon_add.jpg") center / contain no-repeat;
}
.btn_add_number_ok {
  background: url("../images/icon_add_ok.jpg") center / contain no-repeat;
}
.product_number {
  font-size: 0.4rem;
  color: #333333;
  text-align: center;
  border: none;
}
.btn_next_ok {
  display: block;
  margin-top: 0.4rem;
  color: #fff;
  line-height: 1.2rem;
  height: 1.44rem;
  background: url("../images/icon_next.png") center no-repeat;
  background-size: contain;
}
.product_buy_info {
  margin-top: 0.48rem;
  color: #8f8e94;
  font-size: 0.34666667rem;
  overflow: hidden;
}
.search_history {
  margin: 0.53333333rem auto;
  width: 90.625%;
}
.search_history li {
  display: inline-block;
  margin: 0.4rem 0.26666667rem 0 0;
  padding: 0 0.4rem;
  line-height: 0.8rem;
  border: .5px solid #CCCCCC;
  border-radius: 2.66666667rem;
}
.search_history li a {
  color: #333;
  font-size: 0.34666667rem;
}
.search_history .btn_trash {
  float: right;
  width: 0.32rem;
  height: 0.34666667rem;
  background: url("../images/icon_trash.png") center / contain no-repeat;
}
.search_history .tip {
  padding-left: 0.48rem;
  color: #8f8e94;
  font-size: 0.32rem;
  line-height: 0.32rem;
}
.icon_history {
  display: inline-block;
  height: 0.34666667rem;
  background: url("../images/search/icon_history.png") left / contain no-repeat;
}
.tab_content {
  background-color: #fff;
  margin-top: 0.26666667rem;
  padding-bottom: 1.14666667rem;
}
.tab_item li {
  border-bottom: 1px solid #F8F8F8;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.tab_item > ul > li > a {
  padding: 0.13333333rem 0;
  display: table;
  float: left;
  width: 7.2rem;
  height: 2.66666667rem;
}
.product_img {
  width: 2.13333333rem;
  height: 2.13333333rem;
  margin-left: 0.4rem;
  -webkit-align-self: center;
}
.product_info {
  display: table-cell;
  overflow: hidden;
  padding-left: 0.26666667rem;
}
.product_info p {
  width: 4rem;
}
.product_info h3 {
  font-size: 0.34666667rem;
  color: #333;
  width: 4rem;
  margin: 0.4rem 0 0 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.product_info p {
  color: #8F8E94;
  font-size: 0.29333333rem;
  line-height: 1.9;
  padding-left: 0.53333333rem;
}
.product_factory {
  background: url("../images/shop/icon_factory.png") 0 3px no-repeat;
  background-size: 0.37333333rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.product_store {
  background: url("../images/shop/icon_store.png") 0 3px no-repeat;
  background-size: 0.37333333rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.product_price {
  margin-top: 0.53333333rem;
  margin-bottom: 0.64rem;
  color: #FE403B;
  font-size: 0.42666667rem;
  min-height: 0.53333333rem;
  width: 2.05333333rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: right;
}
.old_price {
  color: #666;
  position: absolute;
  top: 1.17333333rem;
  right: 0;
  font-size: 0.29333333rem;
  text-decoration: line-through;
  text-align: right;
}
.btn_add_list,
.btn_product_public {
  margin-top: 0.32rem;
  width: 2.05333333rem;
  height: 0.72rem;
  border: none;
  outline: none;
  background-image: -webkit-linear-gradient(top, #FF5B3B 0%, #FE403B 100%);
  background-image: linear-gradient(-180deg, #FF5B3B 0%, #FE403B 100%);
  box-shadow: 0px 2px 8px 0px rgba(254, 64, 59, 0.5);
  border-radius: 4px;
  color: #FFFFFF;
  letter-spacing: 0px;
  line-height: 11px;
  font-size: 11px;
}
.btn_login {
  display: none;
}
.btn_operate {
  float: right;
  width: 20%;
  position: relative;
  margin-right: 0.4rem;
}
/** tab切换 **/
.tab_nav {
  background-color: #fff;
  border-bottom: 1px solid #eee;
}
.tab_nav:after {
  content: "";
  display: table;
  clear: both;
}
.tab_nav ul {
  overflow: hidden;
  width: 65%;
  margin: 0 auto;
}
.tab_nav ul li {
  float: left;
  width: 50%;
  line-height: 3;
}
.tab_nav a {
  display: block;
  height: 1.14666667rem;
  width: 100%;
  text-align: center;
  color: #8F8E94;
  font-size: 0.34666667rem;
}
.tab_nav_active {
  color: #FE403B!important;
  border-bottom: 1px solid #FE403B;
}
.tab_item {
  display: none;
}
.tab_item section {
  padding: 0.13333333rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 2.66666667rem;
  border-bottom: 1px solid #F8F8F8;
}
.sortbar {
  display: none;
  position: relative;
  height: 1.28rem;
  line-height: 1.28rem;
  background-color: #fff;
}
.sortbar p {
  padding-left: 0.26666667rem;
  float: left;
  color: #333;
  font-size: 0.34666667rem;
}
#btn_sort {
  float: right;
  width: 1.6rem;
  background: none;
  border: none;
  outline: none;
  margin-top: 0.42666667rem;
  font-size: 0.32rem;
  color: #8F8E94;
  padding-right: 0.26666667rem;
}
#btn_sort i {
  margin-left: 0.13333333rem;
  display: inline-block;
  width: 0.29333333rem;
  height: 0.29333333rem;
  background: url("../images/icon_sort.png") 0 0 no-repeat;
  background-size: 0.29333333rem;
  vertical-align: middle;
}
.qualification {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 1.14666667rem;
  background: #FAFAFA;
  font-size: 0.34666667rem;
  color: #333;
  line-height: 3.5;
  text-align: center;
  border-top: 1px solid #ccc;
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 1.33333333rem;
  padding: 0.13333333rem 0;
  background: #fff;
  font-size: 0.26666667rem;
  box-shadow: 0 -1px 2px #e5e5e5;
}
.bot_menu_ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.bot_menu_ul li {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 100%;
  text-align: center;
}
.bot_menu_ul li a {
  display: block;
}
.bot_menu_ul .nav_ico {
  width: 24px;
  height: 22px;
  display: inline-block;
  margin-bottom: 0px;
  background: url(../images/bar_tab_icon.png) no-repeat;
  background-size: 375px 100px;
}
.bot_menu_ul a {
  color: #333333;
}
.bot_menu_ul .world {
  display: block;
  font-size: 12px;
}
.bot_menu_ul .nav_ico1 {
  background-position: -38px -60px;
}
.bot_menu_ul .nav_ico2 {
  background-position: -128px -60px;
}
.bot_menu_ul .nav_ico3 {
  background-position: -222px -60px;
}
.bot_menu_ul .nav_ico4 {
  background-position: -316px -60px;
}
.bot_menu_ul .on .nav_ico1 {
  background-position: -38px -10px;
}
.bot_menu_ul .on .nav_ico2 {
  background-position: -128px -10px;
}
.bot_menu_ul .on .nav_ico3 {
  background-position: -222px -10px;
}
.bot_menu_ul .on .nav_ico4 {
  background-position: -316px -10px;
}
.bot_menu_ul .on .world {
  color: #ff6b82;
}
.bot_menu_ul a:active .world {
  color: #ff6b82;
}
.bot_menu_ul a:active .nav_ico1 {
  background-position: -38px -10px;
}
.bot_menu_ul a:active .nav_ico2 {
  background-position: -128px -10px;
}
.bot_menu_ul a:active .nav_ico3 {
  background-position: -222px -10px;
}
.bot_menu_ul a:active .nav_ico4 {
  background-position: -316px -10px;
}
.btn_group {
  margin-top: 0.53333333rem;
  text-align: center;
}
.btn_group .btn {
  width: 4.37333333rem;
  height: 1.17333333rem;
  border-radius: 5px;
  font-size: 0.4rem;
  border: none;
  outline: none;
  background-color: #fff;
}
.btn_group .btn_cancel {
  margin-right: 0.53333333rem;
  color: #999;
}
.btn_group .btn_submit {
  color: #fff;
  background: #fe4c3b;
}
.btn-sm {
  width: 2.05333333rem;
  height: 0.72rem;
  border-radius: 4px;
  border: none;
  outline: none;
  background-color: #fff;
  text-align: center;
  font-size: 0.29333333rem;
  line-height: 2.2;
}
.btn_disabled {
