/*common ui css*/
.ui_into {
  width: 100%;
  position: relative;
}
.ui_into:after {
  content: "";
  border: 1px solid #ffdd9e;
  border-width: 1px 1px 0 0;
  -webkit-transform: scale(0.8, 1) rotate(45deg);
  transform: scale(0.8, 1) rotate(45deg);
  width: 0.21333333rem;
  height: 0.21333333rem;
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -4px;
}
.ui_fl {
  float: left;
}
.ui_fr {
  float: right;
}
.ui_horscrollpl {
  overflow-x: scroll !important;
  overflow-y: hidden !important;
  -webkit-overflow-scrolling: touch !important;
}
.ui_horscrollpl::-webkit-scrollbar {
  display: none;
}
.ui_verscrollpl {
  overflow-x: hidden !important;
  overflow-y: scroll !important;
  -webkit-overflow-scrolling: touch !important;
}
.ui_verscrollpl::-webkit-scrollbar {
  display: none;
}
.ui_table {
  display: table;
}
.ui_cell {
  display: table-cell;
  vertical-align: middle;
}
body {
  background-color: #fff;
}
.register {
  background-color: #fff;
  width: 100%;
  height: 100%;
}
.cont {
  padding: 0 0.4rem;
}
.from {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  font-size: 0.37333333rem;
  padding-top: 0.26666667rem;
  color: #b2b2b2;
  position: relative;
}
.from input {
  border: none;
  width: 100%;
  text-indent: 0.26666667rem;
  height: 1.33333333rem;
}
.from button {
  background-image: -webkit-linear-gradient(-180deg, #FF5B3B, #FE403B);
  background-image: -webkit-linear-gradient(top, #FF5B3B, #FE403B);
  background-image: linear-gradient(-180deg, #FF5B3B, #FE403B);
  background-color: transparent;
  border: none;
  border-radius: 0.10666667rem;
  font-size: 0.32rem;
  width: 2.08rem;
  height: 0.74666667rem;
  color: #fff;
  position: absolute;
  top: 50%;
  margin-top: -0.37333333rem;
  right: 0.26666667rem;
}
.from button.disable {
  background-image: -webkit-linear-gradient(-180deg, #eee, #eee);
  background-image: -webkit-linear-gradient(top, #eee, #eee);
  background-image: linear-gradient(-180deg, #eee, #eee);
  color: #b2b2b2;
}
.from img {
  position: absolute;
  width: 2.13333333rem;
  height: 0.85333333rem;
  right: 0.26666667rem;
  top: 50%;
  margin-top: -0.42666667rem;
}
.button-bar {
  width: 100%;
  padding: 0 0.53333333rem 0.53333333rem;
}
.button-bar button {
  width: 100%;
  background: #eee;
  box-shadow: 0px 3px 10px 0px rgba(254, 64, 59, 0.5);
  -webkit-box-shadow: 0px 3px 10px 0px rgba(254, 64, 59, 0.5);
  border-radius: 100px;
  font-size: 0.4rem;
  height: 1.2rem;
  line-height: 1.2rem;
  background-image: -webkit-linear-gradient(-180deg, #FF5B3B, #FE403B);
  background-image: -webkit-linear-gradient(top, #FF5B3B, #FE403B);
  background-image: linear-gradient(-180deg, #FF5B3B, #FE403B);
  color: #fff;
}
.button-bar button.disable {
  box-shadow: 0px 3px 10px 0px rgba(238, 238, 238, 0.5);
  -webkit-box-shadow: 0px 3px 10px 0px rgba(238, 238, 238, 0.5);
  color: #b2b2b2;
  background-image: -webkit-linear-gradient(-180deg, #eee, #eee);
  background-image: -webkit-linear-gradient(top, #eee, #eee);
  background-image: linear-gradient(-180deg, #eee, #eee);
}
.button-bar a {
  display: block;
  margin-top: 0.4rem;
  text-align: center;
  width: 100%;
  background: #eee;
  box-shadow: 0px 3px 10px 0px rgba(254, 64, 59, 0.5);
  -webkit-box-shadow: 0px 3px 10px 0px rgba(254, 64, 59, 0.5);
  border-radius: 100px;
  font-size: 0.4rem;
  height: 1.2rem;
  line-height: 1.2rem;
  background-image: -webkit-linear-gradient(-180deg, #FF5B3B, #FE403B);
  background-image: -webkit-linear-gradient(top, #FF5B3B, #FE403B);
  background-image: linear-gradient(-180deg, #FF5B3B, #FE403B);
}
.button-bar a p {
  display: inline-block;
  color: #fff;
  padding-left: 0.58666667rem;
  background-image: url(../images/purchas/button-phone.png);
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 0.48rem auto;
}
.agreement {
  padding: 0.26666667rem 0;
}
.agreement label {
  display: block;
  text-align: left;
  padding-left: 0.53333333rem;
  line-height: 0.53333333rem;
  margin: 0.26666667rem 0.53333333rem;
  font-size: 0.34666667rem;
  position: relative;
  color: #666;
}
.agreement label input:after {
  content: "";
  width: 0.42666667rem;
  height: 0.42666667rem;
  position: absolute;
  top: 50%;
  left: 0;
  background-image: url(../images/purchas/checkbox-off.png);
  background-size: 0.42666667rem auto;
  margin-top: -0.21333333rem;
}
.agreement label input:checked:after {
  content: "";
  background-image: url(../images/purchas/checkbox-on.png);
  background-size: 0.42666667rem auto;
}
.agreement label a {
  color: #FE403B;
}



