
header {
  height: 1.06666667rem;
  line-height: 1.06666667rem;
  text-align: center;
  position: relative;
  border-bottom: 1px solid #ccc;
  background: #fff;
}
header:after {
  content: attr(data-title);
  position: absolute;
  top: 0;
  left: 1.46666667rem;
  right: 1.46666667rem;
  height: 100%;
  font-weight: normal;
  font-size: 0.42666667rem;
}
.head_goback {
  padding-top: 0.21333333rem;
  left: 0.4rem;
  width: 1.06666667rem;
  height: 100%;
  line-height: 100%;
  margin-left: .4rem;
}



